body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

  background-image {
    height: 100vh;
  }
  
  h1 {
    margin-bottom: 0px;
    font-size: 20pt;
  }

  .parallax {
    height: 100%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    }


  /* mobile */
  .App {
      margin: 0 auto;
      display: grid;
      grid-template-columns: 20px 1fr 20px;
      grid-template-rows: repeat(3, auto) 5px;
      grid-row-gap: 40px;
      font-family: "Avenir Next";
      text-align: center;
  }

  .header {
      background-color: white;
      grid-column: 2/3;
      grid-row: 1/2;
      padding-bottom: 20px;
      padding-right: 5px;
      padding-left: 5px;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
  }

  .searchDiv {
      grid-row: 2/3;
      grid-column: 2/3;
      display: grid;
      grid-template-columns: repeat(3, auto);
      grid-template-rows: auto;
  }

  #searchWord {
    grid-column: 2/3;
    background-color: white;
    margin-bottom: 0px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding-top: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin-top: 0px;
  }

.inputFields {
    grid-column: 2/3;
    background-color: white;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    padding-bottom: 5px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.resultsDiv {
    background-color: white;
    grid-column: 2/3;
    grid-row: 3/4;
    border-radius: 10px;
    padding-left: 5px;
    padding-right: 5px;
}

@media (max-width: 1100px) {
input {
    border: 0.5px solid black;
    border-radius: 5px;
}

button {
    border: 0.5px solid black;
    border-radius: 5px;
    background-color: transparent;
}
}


  /* desktop */
  @media (min-width: 1100px) {

    h1 {
        margin-bottom: 0px;      
    }

  .App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 125px repeat(3, auto);
    grid-row-gap: 40px;
    font-family: "Avenir Next";
  }
  
  .header {
    grid-column: 3/span 4;
    grid-row: 1/2;
    text-align: center;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  
  .searchDiv {
    grid-row: 2/3;
    grid-column: 4/span 2;
    text-align: center;
    border-radius: 10px;
    background-color: white;
  }
  
  .resultsDiv {
    grid-row: 3/4;
    grid-column: 3/span 4;
    text-align: center;
    border-radius: 10px;
  }
  
}
p {
    text-align: center;
    margin-bottom: 0px;
    margin-top: 5px;
}

button {
    margin-bottom: 10px;
}
.individualResult {
    margin-bottom: 20px;
}
.resultsDiv {
    background-color: white;
}
